const About = () => {
  return (
    <section className="bg-white w-full container">
      <div className="flex justify-center items-center p-4">
        <div className="flex space-x-4">
          {/* First Div with Image */}
          <div className="w-1/3">
            <img
              src="your-image-url.jpg"
              alt="Description"
              className="w-full h-auto object-cover"
            />
          </div>

          {/* Second Div with 2 nested divs (Text and bordered content) */}
          <div className="w-2/3">
            <div className="flex flex-col space-y-4">
              {/* First div with text */}
              <div className="text-lg font-semibold">
                <p>Your text content goes here.</p>
              </div>

              {/* Second div with border and button */}
              <div className="border-l-4 border-gray-500 p-4 flex flex-col">
                <p className="mb-2">Text inside the bordered div.</p>
                <button className="px-4 py-2 bg-blue-500 text-white rounded">
                  Button
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
